export default {
  data() {
    return {
      userInfo: {},
      fname: '',//公司名称
      newsPassword: '',//用户的新密码
      repeatPassword: '',//用户重新输入的新密码
      oldPassword: '',//用户的旧密码
      uPhone: '',//用户的手机号
    }
  },
  mounted() {
    this.userInfo = JSON.parse(this.$app_store.state.userInfo);//获取用户信息
    this.uPhone = this.userInfo.uphone;
    this.fname = this.userInfo.uname;
  },
  methods: {
    fallback() {
      this.$router.go(-1)
    },
    updatePasswordByOld() {
      let self = this;
      // 密码 字母+数字
      let pwdReg = /(?=^.*?\d)(?=^.*?[a-zA-Z])^[0-9a-zA-Z]{6,16}$/;
      if(!pwdReg.test(this.newsPassword)){
        this.$vux.toast.text('请输入字母或数字组成的密码', 'top');
        this.newsPassword = '';
        this.repeatPassword = '';
        return false
      }
      if (self.newsPassword !== self.repeatPassword) {
        self.$vux.toast.text('两次输入的密码不致，请重新输入', 'top');
        return
      }
      this.$Ice_UserService.updatePasswordByOld(this.uPhone, this.oldPassword, this.repeatPassword, new IceCallback(
        function (result) {
          if (result.code === 0) {
            self.$vux.toast.text(result.msg, 'top');
            // 退出这个页面
            setTimeout(() => {
              self.$router.go(-1);
            }, 1000);
          } else {
            // 发送失败
            self.$vux.toast.text(result.msg, 'top');
            self.$dialog.loading.close();
          }
        },
        function (error) {
          self.$dialog.loading.close();
          self.$vux.toast.text('服务器连接失败, 请稍后重试', 'top');
        }
      ));
    }
  }
}



