import {
  DICT,
  AREA,
  CITYS,
  CURRENT_CITY, IS_SHOW_SIDEBAR, TABBAR_INDEX, AVATAR_URL, CITY_CODE
} from '../store/mutation-types'
import { subString } from '../utils/stringUtil'
export default {
  computed: {
    isShowSidebar: {
      get: function () {
        this.isUnreadMsg();
        return this.$app_store.state.isShowSidebar
      },
      set: function () {
        this.$app_store.commit(IS_SHOW_SIDEBAR, false);
      }
    },
    isAvatar: {
      get: function () {
        return this.$app_store.state.avatar
      },
      set: function () {
        // this.$app_store.commit(IS_SHOW_SIDEBAR, false);
      }
    },
    tabBarIndex: {
      get: function () {
        this.active = this.$app_store.state.tabBarIndex;
        return this.$app_store.state.tabBarIndex
      },
      set: function () {
        this.$app_store.commit(TABBAR_INDEX, this.active);
      }
    }
  },
  data() {
    return {
      popShow: false,
      isNewMsg:true,// 是否有新的消息
      cLevel: [],// 认证等级
      avatar: '',// 头像
      compName: '',// 企业名
      compId: '',
      transitionName: '',
      compInfo: null,
      userId: this.$app_store.getters.userId,
      active: 1,
      cityInfo: {}
    }
  },
  activated() {
    this.isUnreadMsg();
    this.compId = this.$app_store.getters.compId;
    this.compInfo = JSON.parse(this.$app_store.state.compInfo);
    if(this.compInfo !== undefined && this.compInfo !== null) {
      this.compName = this.compInfo.fname;
      this.avatar = this.compInfo.logoPath;
      this.$app_store.commit(AVATAR_URL,this.avatar);
      this.computeLevel();
      console.log(this.compName);
    } else {
      this.avatar = require('../assets/images/small/avatar.png');
    }
  },
  mounted() {
    this.$app_store.commit(CURRENT_CITY, '长沙市');
    this.$app_store.commit(CITY_CODE, '104301');
    // this.initLocatingCity();
    // 是否有新的消息
    this.isUnreadMsg();
    // 初始化基础数据字典
    this.initBaseData();
    // 初始化地区
    this.initAreaData();
    // 初始化
    this.initAreaH5Data();
  },
  methods: {
    initLocatingCity() {
      // 获取当前城市 如无: 默认选择长沙
      let info =  localStorage.getItem('cityInfo') || null;
      if(info!== null) {
        this.cityInfo = JSON.parse(info);
      } else {
        let obj = {
          currentCity: '长沙',
          cityCode: '104301'
        };
        this.cityInfo = localStorage.setItem('cityInfo',JSON.stringify(obj));
        this.popShow = true
      }
      this.$app_store.commit(CURRENT_CITY, this.cityInfo.currentCity);
      this.$app_store.commit(CITY_CODE, this.cityInfo.cityCode);
    },
    tabItemClick(index) {
      this.active = index
    },
    isUnreadMsg() {
      // 查询个人消息模块内是否有新的信息
      let self = this;
      this.$Ice_MessageService.isUnreadMsg(self.userId,new IceCallback(
        function(result){
          if(result>0) {
            self.isNewMsg = false;
          }else {
            self.isNewMsg = true;
          }

      },function(error){
        //失败
      }))
    },
    initBaseData() {
      let self = this;
      self.$Ice_SystemService.getBaseUnit(
        new IceCallback(
          function (result) {
            if(result.code === 0) {
              self.$app_store.commit(DICT, JSON.stringify(result.data));
            } else {
              self.$vux.toast.text('货物类型获取失败', 'top');
            }
          },
          function (error) {
            setTimeout(() => {
              self.initBaseData();
            }, 15000);
          }
        )
      );
    },
    initAreaData() {
      let self = this;
      self.$Ice_SystemService.getAreaCode(
        new IceCallback(
          function (result) {
            if(result.code === 0) {
              self.$app_store.commit(AREA, JSON.stringify(result.data.children));
            }
          },
          function (error) {
            setTimeout(() => {
              self.initAreaData();
            }, 15000);
          }
        )
      );
    },
    initAreaH5Data() {
      let self = this;
      self.$Ice_SystemService.getAreaH5Data(
        new IceCallback(
          function (result) {
            if(result.code === 0) {
              self.$app_store.commit(CITYS, result.obj);
            }
          },
          function (error) {
            setTimeout(() => {
              self.initAreaData();
            }, 15000);
          }
        )
      );
    },
    computeLevel() {
      let result = []; // 返回的是一个数组,用来遍历输出星星
      let score = Math.floor(this.compInfo.creadit) / 2; // 计算所有星星的数量
      // let hasDecimal = score % 1 !== 0; // 非整数星星判断
      let integer = Math.floor(score); // 整数星星判断
      for (let i = 0; i < integer; i++) { // 整数星星使用on
        result.push( require('../assets/images/small/star36_on@2x.png'));
      }
      // if (hasDecimal) { // 半星
      //   result.push("half");
      // }
      while (result.length < 5) {// 余下的用无星星补全,使用off
        result.push(require('../assets/images/small/star36_off@2x.png'));
      }
      this.cLevel = result;
    },
    subStr: function (str, len) {
      return subString(str, len)
    },
    toDriverAmd() {
      this.$router.push({
        path: '/center/driverMgr/index'
      })
    },
    // 业务员管理
    toSalesManage() {
      this.$router.push({
        name: 'salesManages'
      })
    },
    toMyCircle() {
      this.$router.push({
        path: '/center/myCircle/index'
      })
    },
    toMyRelease() {
      this.$router.push({
        path: '/order/acceptOrders'
      })
    },
    tocommonlyRoute() {
      this.$router.push({
        path: '/center/commonlyRoute/index'
      })
    },
    toblacklist() {
      this.$router.push({
        path: '/center/blacklist/index'
      })
    },
    tomyInformation() {
      this.$router.push({
        path: '/center/myInformation/index'
      })
    },
    jUserInfo() {
      this.$router.push({
        path: '/userInfo',
        query: {
          isYourCompInfo: true,    //row.hid为变量
        }
      })
    },
    toSetting() {
      this.$router.push({
        path: '/center/setting/index'
      })
    },
    todimensionalCode() {
      this.$router.push({
        path: 'home/dimensionalCode'
      })
    },
    handleActive(position) {
      this.$app_store.commit(TABBAR_INDEX, position);
    },
    // navBarClick(index) {
    //   let path = '/';
    //   switch (index) {
    //     case 0:
    //       path = '/';
    //       break;
    //     case 1:
    //       path = '/information';
    //       break;
    //     case 2:
    //       path = '/circle';
    //       break;
    //     case 3:
    //       path = '/order';
    //       break;
    //   }
    //   this.handleActive(index);
    //   this.$router.push(path)
    // },
  },
  watch: {
    active(newActive, oldActive) {
      let path = '/';
      switch (newActive) {
        case 0:
          path = '/';
          break;
        case 1:
          path = '/information';
          break;
        case 2:
          path = '/circle';
          break;
        case 3:
          path = '/order';
          break;
      }
      this.handleActive(newActive);
      this.$router.push(path)
    }
  }
}



