
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import {alertContent} from "../../utils/enum";
import {TABBAR_INDEX} from "../../store/mutation-types";
import {IS_SHOW_SIDEBAR} from "../../store/mutation-types";
import { subString } from '../../utils/stringUtil'
export default {
components: {
Tab,
TabItem
},
data() {
return {
index: '0',
year: '',
isShowNoData: false, // 无数据显示
avatar: this.$app_store.state.avatarUrl,
QueryParam: new redundancy.QueryParam(),
page: new redundancy.Page(),
userId: this.$app_store.getters.userId,
releaseList: [], // 订单发布列表
total: 0,// 发布订单总条数
loading: false,
finished: false,

tabList: [{
value: '1',
name: '抢单',
isSelected: true
}, {
name: '取货',
value: '3',
isSelected: false
}, {
name: '签收',
value: '4',
isSelected: false
}, {
name: '全部',
value: '',
isSelected: false
}]
}
},
created() {

},
mounted() {
this.year = new Date().getFullYear();
this.col1Data = [];
for(let i = 0;i<10;i++) {
let year = this.year - i;
let obj = {
value: year,
text: year
};
this.col1Data.push(obj);
}

this.initQueryConditions('1');
this.picker = this.$createPicker({
title: '选择年份',
data: [this.col1Data],
onSelect: (selectedVal, selectedIndex, selectedText) => {
this.year = selectedVal[0];

this.initQueryConditions(this.index);
console.log(this.page)

this.releaseList = [];



this.queryMyRecvOrder();
},
onCancel: () => {

}
})

},
methods: {
showPicker () {
this.picker.show()
},
subStr: function (str, len) {
return subString(str, len)
},
statusToChinese(status) {
let cont = '';
switch (status) {
case 1:
cont = '已抢单';
break;
case 2:
cont = '已中转';
break;
case 3:
cont = '已取货';
break;
case 4:
cont = '已签收';
break;
case 6:
cont = '评价';
break;
case 7:
cont = '抢单成功';
break;
case 8:
cont = '已完成';
break;
default:
break
}
return cont
},
tabItemClick(item) {
this.tabList.forEach((value, index, arr) => {
value.isSelected = false
});
item.isSelected = true;

this.initQueryConditions(item.value);

this.releaseList = [];


this.onLoad();
},
avatarClick() {
this.$app_store.commit(IS_SHOW_SIDEBAR, true);
},

entryTrip(orderno) {
this.$router.push({
name: 'entryTravel',
query: {
orderno: orderno
}
})
},

printOrder(item) {
let self = this;
this.$Ice_myOrderService.getTrancCode(this.userId,item.orderno,item.tstatus,new IceCallback(
function(result){
if (result.code === 0) {
let data = JSON.parse(result.obj.trancCode);
let	dataImg = data.data.nginx + data.data.relativeAddr;
self.$router.push({
name: 'preview',
query: {
url: dataImg
}
})
} else {
self.$vux.toast.text('转运单获取失败', 'top');
}
}
))
},
initQueryConditions(status) {

this.page.pageSize = 10; 
this.page.pageIndex = 1; 
this.page.totalItems = 0;
this.page.totalPageCount = 0;

this.QueryParam.revicompid = '';
this.QueryParam.origin = '';
this.QueryParam.destination = '';
this.QueryParam.time = '';
this.QueryParam.tstatus = status;
this.QueryParam.year = this.year;
},

toComPInfo(item) {
this.$router.push({
path: '/userInfo',
query: {
isYourCompInfo: false,
id: item.pubcompid,
status: 6
}
})
},
toReleaseDetails(item) {
item.type = 1;
this.$router.push({
name: 'orderDetail',
query: {
params : JSON.stringify(item)
},
})
},
toAgainRelease(item){
this.$router.push({
name: 'againRelease',
query: {
orderId: item.orderno
}

})
},

cancelOrder(item, index) {
let self = this;
this.message.showAlert(this, alertContent.CANCEL_ORDER)
.then(() => {
self.$Ice_myOrderService.cancelRobbing(item.orderno,self.userId, new IceCallback(
function (result) {
if (result.code === 0) {
self.releaseList.splice(index,1);
self.$vux.toast.text('订单取消成功 !', 'top');
} else {
self.$vux.toast.text(result.msg, 'top');
}
},
function (error) {
self.$vux.toast.text('服务器连接失败, 请稍后重试', 'top');
}
))
})
.catch(() => {

})
},

queryMyRecvOrder() {
let self = this;
this.$Ice_redundancyService.queryMyRecvOrder(this.userId, this.QueryParam,this.page, new IceCallback(
function (result) {
self.loading = false;
if (result.code === 0) {
self.page.pageIndex += 1;
if(result.obj !== '[]') {
self.releaseList = self.releaseList.concat(JSON.parse(result.obj));
self.finished = false;
} else{
self.finished = true;
}
self.total = result.totalItems;
if (self.releaseList.length >= self.total) {
self.finished = true;
}
} else {
self.finished = true;
}
if (self.releaseList !== null && self.releaseList.length > 0) {
self.isShowNoData = false;
} else {
self.isShowNoData = true;
}
},
function (error) {
self.loading = false;
self.finished = true;
}
))
},
onLoad() {
this.queryMyRecvOrder();
},
showCodamt(codamt,insureamt) {
let str = '';
if(codamt > 0) {
str = '代收金额: '+ codamt
}else {
str = '无代收'
}
if(insureamt > 0) {
str += ',保价金额: ' + insureamt
} else {
str += ',无保价'
}
return str
},

toSchedulePlayBack(item) {
this.$router.push({
name: 'schedulePlayBack',
params: item
})
},

toPickGoodsPic(item) {
if(item.puimg === '0') {
this.$vux.toast.text('暂无取货照片', 'top');
return
}
this.$router.push({
name: 'pickGoodsPic',
params: item
})
},
fallback() {
this.$router.go(-1)
},

addressSubStr(str) {
return  str.split(',')[1]
}
}
}



