export default {
  data(){
    return{
      userInfo: {}
    }
  },
  mounted() {
    this.userInfo = JSON.parse(this.$app_store.state.userInfo) ;
  },
  methods:{
    fallback() {
      this.$router.go(-1)
    },
    toaccountUpdataPwd(){
      this.$router.push({
        path:'/center/setting/toaccountUpdataPwd'
      })
    },
    toupdataPhone(){
      this.$router.push({
        path:'/center/setting/updataPhone'
      })
    }
  }
}



